@keyframes spinner {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  .spinner-container{
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 50%;
  }
  .loading-spinner {
    width: 50px;
    height: 50px;
    border: 10px solid #f3f3f3; /* Light grey */
    border-top: 10px solid #2179d8; /* Black */
    border-radius: 50%;
    animation: spinner 1.0s linear infinite;
  }